import { Link } from "react-router-dom"
import { useNavigate } from "react-router-dom";
var load;
window.onload=function(){
  load=true;

}
const Return=()=>{
const navigate=useNavigate();
if(load==true)
{
  navigate("/")
  load=false;
}
    return (
        
               <div style={{textAlign:"left",marginLeft:"20%",marginTop:"5%",width:"60%"}}>
                   <p style={{fontSize:"25px"}}>RETURN POLICY</p>
                   <Link to="/" style={{color:"#9a031e"}}>Home</Link>
                   <hr style={{color:"black",backgroundColor:"black",height:"2px"}}/>
                   <p style={{lineHeight:"50px",color:"#1b3a4b"}}>
                   <span style={{fontWeight:"bold",fontSize:"20px"}}>1. Introduction</span>- At https://nextgen-project.tech, we strive to provide a seamless shopping experience. If you are not satisfied with your purchase, you may be eligible to return the product under the terms outlined below.
                   <br/><br/>
                   <span style={{fontWeight:"bold",fontSize:"20px"}}>2. Eligibility for Returns</span>-

Returns must be initiated within 15 days of delivery.

Products must be unused, undamaged, and returned in their original packaging with all accessories and tags.

Certain products (e.g., perishable goods, personal care items, customized products) may not be eligible for return. Please check the product description for details.
                   <br/><br/>
                   <span style={{fontWeight:"bold",fontSize:"20px"}}>3. Return Process</span>-

To initiate a return, visit the ‘My Orders’ section on the Platform and submit a return request.

Provide a valid reason for the return and attach supporting images (if applicable).

Our team will review the request and provide return shipping instructions.
                   <br/><br/>
                   <span style={{fontWeight:"bold",fontSize:"20px"}}>4. Refund Policy</span>-

Once we receive and inspect the returned item, a refund will be processed within 15 business days.

Refunds will be issued via the original payment method or as store credit, depending on your preference.
                   <br/><br/>
                   <span style={{fontWeight:"bold",fontSize:"20px"}}>5. Exchange Policy</span>-

Exchanges are subject to product availability.

If the desired item is unavailable, you may opt for a refund or store credit.
                   <br/><br/>
                   <span style={{fontWeight:"bold",fontSize:"20px"}}>6. Defective or Incorrect Products</span>-

If you receive a damaged or incorrect item, report it within 2 days of delivery.

We will arrange for a replacement or full refund at no additional cost.
                   <br/><br/>
                   <span style={{fontWeight:"bold",fontSize:"20px"}}>7. Return Shipping</span>-

Customers may be responsible for return shipping costs unless the return is due to an error on our part.

We recommend using a trackable shipping service to ensure the safe return of items.
                   
            </p>
        </div>
    )
}

export default Return